
.main-container-screen2 {
    height: auto;
    // border: 2px solid red;
   
   
    h6 {
        font-style: normal;
        font-weight: 400;
        font-size: clamp(9.5px,3vw,14px);
        line-height: 21px;
        margin: 2vh 12%;

    }
    .back-forword a {
        font-size: calc(.7rem + .6vw);
        color: #800080;
        font-weight: 600;
        cursor: pointer;
        text-decoration: none;
        span {
            font-size: calc(.6rem + .6vw);
            font-weight: 300;
        }
    }
}
.back-color{
    background-color: whitesmoke;
    padding: 0 12% 10px 12%;
    
}
.acount-cantainer-section2 {
    background: whitesmoke;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .account-chaile {
        width: 45%;
        margin: 2vh 0;
        &:first-child {
            background: #FFFFFF;
            box-shadow: 0 3px 100px rgba(0, 0, 0, 0.1);
            border-radius: 20px;
        }
        &:nth-child(2) {

            div {
                display: flex;
                align-content: center;
                justify-content: center;
                padding: 2px 3px;
                margin-top: 10px;
                background: linear-gradient(270deg, #800080 0%, #FF864C 100%);
                border-radius: 5px;
                p {
                    font-weight: 500;
                    font-size: clamp(1.2rem , 1.4vw , 1.9vw);
                    line-height: 30px;
                    background-color: white;
                    border-radius: 5px;
                    width: 100%;
                    text-align: center;
                    padding: 1vw;
                    /* identical to box height */

                    letter-spacing: 1.5em;

                    color: #800080;
                }
            }
        }
    }
}
.h4-heading {
    font-size: clamp(1.4rem , 1vw , 1.5vw);
    color: #800080;
}

.accont-child-first-row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 1vw;
    .row-names {
        margin: 0 0.1vw;
        padding: 1vw 0.4vw;
        // border: 1px solid red;
        span {
            font-size: calc(.5rem + .5vw);
            color: #800080;
        }
        p {
            font-style: normal;
            font-weight: 500;
            font-size: calc(.7rem + .7vw);
            margin-top: 0.5vh;
            /* identical to box height */

            color: #000000;
        }
        &:nth-child(4) {
            // align-items: center;
            display: flex;
            align-items: center;
            justify-content: center;
            .account-Withdraw {
                font-size: calc(.5rem + .5vw);
                color: white;
                background: #800080;
                border-radius: 20px;
                padding: 10px;
                outline: none;
                border: none;
            }
        }

    }
}
.h4-heading2,a{
    font-size:clamp(16px,1vw,22px);
    color: #800080;
    margin: 10px 0;
    font-weight: 500;
    cursor: pointer;
    text-decoration: none;
}
.how-dos-work-container{
    margin-top: 2vh;
}
.word-container {
    display: flex;
    align-items: center;
    justify-content: left;
    flex-wrap: wrap;
    padding: 1vh 0;
    .workers-frame {
        display: flex;
        width: 44%;
        margin: 1vw;
        // border: 1px solid red;
        .worker-ing {
            width: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            .img-box{
            width: 55px;
            height: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            // border: 1px solid green;
            border-radius: 50%;
            background-color: white;
        }
            img {
                width: auto;
                height: auto;
            }
        }
        .worker-desctiptions {
            padding: 2px 10px;
            h5 {
                font-size: clamp(16px,1vw ,19px);
                font-weight: 500;
            }
            p {
                margin-top: 0.5vh;
                font-size: 16px;
                line-height: 24px;

                color: #4D4D4D;
            }
        }
    }

}
@media (max-width:900px) {
    .main-container-screen2 {

        padding: 0 0%;
    }
    .accont-child-first-row {

        justify-content: space-between;

        padding: 1vw;
        .row-names {
            margin: 1vw 10%;
            padding: 1vw;
        }
    }
    .account-chaile {
        width: 100% !important;
    }
    .word-container {
        flex-wrap: wrap;
        .workers-frame {

            width: 100%;
        }
    }
    .back-color{
       
        padding: 0 5% 10px 5%;
        
    }
}
@media (max-width:400px) {
    .accont-child-first-row {

        .row-names {
            margin: 1vw 8%;
            padding: 1vw;
        }
    }
}
